.container{
    display: grid;
    grid-template: 30px 100px 108px;
    grid-template-areas:    "top"
                            "mid"
                            "bot"
    ;
    border: rgb(50, 146, 211) solid 2px;
    border-radius: 20px;
    gap: 5px;               
}

.containerButton{
    grid-area: top;
    display: flex;
    justify-content: space-around;
}
.button1{
  margin: 5px;
  height: 35px;
  width: 35px;
  border-radius: 20px;
  justify-content: center;
}

.button2{
  display: flexbox;
  justify-content: center;
  flex-wrap: wrap;
  margin: 5px;
  height: 35px;
  width: 35px;
  border-radius: 20px;
  background-color: rgb(34, 223, 34);
}

.button2:hover{
  background-color:red;
  cursor:pointer;
  color:white;
  }


.containerImagen{
    grid-area: mid;
    display: block;
    justify-self: center;
}

img{
    border-radius: 150px;
    width: 140px;
    height: 140px;
    display: block;
    justify-self: center;
}

.containerText{
  grid-area: bot; 
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif; 
  border-radius: 20px;
  background-color: white;
}

.text{
  color: rgb(54, 46, 46);
  text-align: center;
  font-size: 16px;
  margin: 2% 5% 5% 5%;
  cursor: pointer;
}

.text:hover{
  border: rgb(50, 146, 211) solid 1px;
  border-radius: 13px;
  text-shadow: 2px 1px 1px rgb(50, 146, 211);
}

@font-face {
    font-family: 'get_schwifty';
    src: url("../../../assets/fonts/get_schwifty.ttf");
    font-style: normal;
    font-weight: 10px;
  }